.header
    width: 100%
    height: 50px
    background-color: #fff
    display: flex
    justify-content: space-between
    align-items: center
    &__logo
        width: 150px
        height: 100%
        padding: 7px 0 0
        font-size: 25px
        color: #000
        text-align: center
        text-decoration: none
    &__nav
        display: flex
        width: 240px
        justify-content: flex-end
        align-items: center
        text-align: center
        transform: skewX(-10deg)
        &__link
            width: 120px
            height: 50px
            color: #000
            font-size: 18px
            padding-top: 11px
            border-left: 1px solid #000
            transition: 0.3s all
            cursor: pointer
            text-decoration: none
            &:first-child
                border: none
            &:hover
                border-bottom: 4px solid #666666
                padding-top: 6px
            &.link_active
                border-bottom: 4px solid #666666
                padding-top: 6px
    &__search
        width: 265px
        height: 80%
        position: relative
        margin-right: 25px
        padding: 8px
        background-color: #666666
        border: none
        border-radius: 25px
        transition: 0.7s all
        &:focus
            outline: none
            box-shadow: 2px 4px 4px #000

